import React from 'react'
import styled from 'styled-components'
import { Link } from '../../components/Link'
import { HelpIcon } from '../../components/Svg'
import { Modal } from '../Modal'
import WalletCard from './WalletCard'
import config from './config'
import { Login } from './types'

interface Props {
  login: Login
  onDismiss?: () => void
}

const HelpLink = styled(Link)`
  display: flex;
  align-self: center;
  align-items: center;
  margin-top: 24px;
`

const WalletCards = styled.div`
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
`

const ConnectModal: React.FC<Props> = ({ login, onDismiss = () => null }) => (
  <Modal title="Connect wallet" onDismiss={onDismiss}>
    <WalletCards>
      {config.map((entry) => (
        <WalletCard key={entry.title} login={login} walletConfig={entry} onDismiss={onDismiss} />
      ))}
    </WalletCards>
    <HelpLink href="https://docs.pancakeswap.finance/get-started/connection-guide" external>
      <HelpIcon color="primary" mr="6px" />
      Learn how to connect
    </HelpLink>
  </Modal>
)

export default ConnectModal
