export const useDIBVault = () => {
  const {
    totalShares: totalSharesAsString,
    pricePerFullShare: pricePerFullShareAsString,
    totalDIBInVault: totalDIBInVaultAsString,
    estimatedDIBBountyReward: estimatedDIBBountyRewardAsString,
    totalPendingDIBHarvest: totalPendingDIBHarvestAsString,
    fees: { performanceFee, callFee, withdrawalFee, withdrawalFeePeriod },
    userData: {
      isLoading,
      userShares: userSharesAsString,
      dibAtLastUserAction: dibAtLastUserActionAsString,
      lastDepositedTime,
      lastUserActionTime,
    },
  } = useSelector((state: State) => state.pools.dibVault)

  const estimatedDIBBountyReward = useMemo(() => {
    return new BigNumber(estimatedDIBBountyRewardAsString)
  }, [estimatedDIBBountyRewardAsString])

  const totalPendingDIBHarvest = useMemo(() => {
    return new BigNumber(totalPendingDIBHarvestAsString)
  }, [totalPendingDIBHarvestAsString])

  const totalShares = useMemo(() => {
    return new BigNumber(totalSharesAsString)
  }, [totalSharesAsString])

  const pricePerFullShare = useMemo(() => {
    return new BigNumber(pricePerFullShareAsString)
  }, [pricePerFullShareAsString])

  const totalDIBInVault = useMemo(() => {
    return new BigNumber(totalDIBInVaultAsString)
  }, [totalDIBInVaultAsString])

  const userShares = useMemo(() => {
    return new BigNumber(userSharesAsString)
  }, [userSharesAsString])

  const dibAtLastUserAction = useMemo(() => {
    return new BigNumber(dibAtLastUserActionAsString)
  }, [dibAtLastUserActionAsString])

  return {
    totalShares,
    pricePerFullShare,
    totalDIBInVault,
    estimatedDIBBountyReward,
    totalPendingDIBHarvest,
    fees: {
      performanceFee,
      callFee,
      withdrawalFee,
      withdrawalFeePeriod,
    },
    userData: {
      isLoading,
      userShares,
      dibAtLastUserAction,
      lastDepositedTime,
      lastUserActionTime,
    },
  }
}

