/* eslint-disable  import/no-cycle */

import React from 'react'
import styled, { keyframes } from 'styled-components'
import { Link } from 'react-router-dom'

import Flex from '../../../components/Box/Flex'
import { LogoIcon as LogoWithText } from '../icons'

interface Props {
  href: string
}

const blink = keyframes`
  0%,  100% { transform: scaleY(1); } 
  50% { transform:  scaleY(0.1); } 
`

const StyledLink = styled(Link)<any>`
  display: flex;
  align-items: center;
  .mobile-icon {
    width: 32px;
    ${({ theme }) => theme.mediaQueries.nav} {
      display: none;
    }
  }
  .desktop-icon {
    width: 160px;
    display: none;
    ${({ theme }) => theme.mediaQueries.nav} {
      display: block;
    }
  }
  .right-eye {
    animation-delay: 20ms;
  }
  &:hover {
    .left-eye,
    .right-eye {
      transform-origin: center 60%;
      animation-name: ${blink};
      animation-duration: 350ms;
      animation-iteration-count: 1;
    }
  }
`

const Logo: React.FC<Props> = ({ href }) => {
  const isAbsoluteUrl = href.startsWith('http')
  const innerLogo = (
    <>
      <LogoWithText />
    </>
  )

  return (
    <Flex className="desktop-icon">
      {isAbsoluteUrl ? (
        <StyledLink as="a" href={href} aria-label="Dog In Black home page">
          {innerLogo}
        </StyledLink>
      ) : (
        <StyledLink to={href} aria-label="Dog In Black home page">
          {innerLogo}
        </StyledLink>
      )}
    </Flex>
  )
}

export default Logo
