import { ChainId } from 'sdk'
import addresses from 'config/constants/contracts'
import tokens from 'config/constants/tokens'
import { Address } from 'config/constants/types'
import { CHAIN_ID } from 'config'

export const getAddress = (address: Address): string => {
  return address[CHAIN_ID] || address[ChainId.MAINNET]
}

export const getDIBAddress = () => {
  const address = getAddress(tokens.dib.address)
  return address
}
export const getMasterChefAddress = () => {
  return getAddress(addresses.masterChef)
}
export const getMulticallAddress = () => {
  return getAddress(addresses.multiCall)
}
export const getWethAddress = () => {
  return getAddress(tokens.weth.address)
}
export const getReferralsAddress = () => {
  return getAddress(addresses.referral)
}
export const getGameFactoryAddress = () => getAddress(addresses.gameFactory)
