import React from 'react'
import styled from 'styled-components'

import Logo from 'uikit/components/Svg/Icons/Logo'
import { BASE_SWAP_URL } from 'config'
import { getWethAddress, getDIBAddress } from 'utils/addressHelpers'

import Text from '../../../components/Text/Text'
import Skeleton from '../../../components/Skeleton/Skeleton'

interface Props {
  dibPriceUsd?: number
  className?: string
}

const PriceLink = styled.a`
  display: flex;
  align-items: center;
  color: #fff;
  gap: 10px;

  img {
    width: 30px;
    transition: transform 0.3s;
  }
  :hover {
    img {
      transform: scale(1.2);
    }
  }
`

const DIBPrice: React.FC<Props> = ({ dibPriceUsd, className }) => {
  return dibPriceUsd ? (
    <PriceLink
      className={className}
      href={`${BASE_SWAP_URL}?inputCurrency=${getWethAddress()}&outputCurrency=${getDIBAddress()}`}
      target="_blank"
    >
      <div>Buy</div> <Logo />
      <Text color="textSubtle" bold>{`$${dibPriceUsd.toFixed(3)}`}</Text>
    </PriceLink>
  ) : (
    <Skeleton width={80} height={24} className={className} />
  )
}

export default React.memo(DIBPrice)
