/* eslint-disable no-param-reassign */
import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import axios from 'axios'

import { WHITELIST_URL } from 'config'

const initialState = {
  merkleTree: {
    values: [],
  },
  loading: false,
}

export const fetchWhitelistMerkleTreeAsync = () => async (dispatch) => {
  try {
    const { data } = await axios.get(WHITELIST_URL)
    dispatch(setWhitelist(data))
  } catch (e) {
    console.error('e', e)
  }
}

export const whitelistSlice = createSlice({
  name: 'Whitelist',
  initialState,
  reducers: {
    setWhitelist: (state, action: PayloadAction<any>) => {
      state.merkleTree = action.payload
      state.loading = false
    },
  },
})

export const { setWhitelist } = whitelistSlice.actions

export default whitelistSlice.reducer
