import { BASE_SCAN_URL } from 'config'

export const getScanUrl = (value: any, type: 'address' | 'token' | 'tx' | 'block' | 'blockCountdown' = 'address') => {
  const prefixByType = {
    address: 'address',
    token: 'token',
    tx: 'tx',
    block: 'block',
    blockCountdown: 'block/countdown',
  }

  const prefix = prefixByType[type]

  return `${BASE_SCAN_URL}/${prefix}/${value}`
}

export const getScanAddressUrl = (address: string) => getScanUrl(address)
export const getScanTransactionUrl = (hash: string) => getScanUrl(hash, 'tx')
export const getScanBlockNumberUrl = (block: string | number) => getScanUrl(block, 'block')
export const getScanBlockCountdownUrl = (block: string | number) => getScanUrl(block, 'blockCountdown')
export const getScanTokenUrl = (address: string) => getScanUrl(address, 'token')
