import { ContextApi } from 'contexts/Localization/types'
import { PageMeta } from './types'

export const DEFAULT_META: PageMeta = {
  title: 'DIB Yield',
  description: '',
  image: 'https://dibyield.com/images/hero.png',
}

export const getCustomMeta = (path: string, t: ContextApi['t']): PageMeta => {
  switch (path) {
    case '/':
      return {
        title: `${t('Home')} | ${t('DIB Yield')}`,
      }
    case '/farms':
      return {
        title: `${t('Farms')} | ${t('DIB Yield')}`,
      }
    case '/pools':
      return {
        title: `${t('Pools')} | ${t('DIB Yield')}`,
      }
    default:
      return null
  }
}
